/* Slideshow */

.slideshow {
    width: 100%;
    max-width: 100%;
    margin: 60px auto 30px auto;
    overflow: visible;
    box-sizing: border-box;
    /*max-width: 500px;*/
  }
  
  .slideshowSlider {
    white-space: nowrap;
    transition: ease 1000ms;
    max-width: 100%;
  }
  
  .slide {
    display: inline-block;
    vertical-align: top;
    height: 350px;
    width: 100%;
    max-width: 100%;
    border-radius: 40px;
    overflow: visible;
  }
  
  .slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  /* Buttons */
  
  .slideshowDots {
    text-align: center;
  }
  
  .slideshowDot {
    display: inline-block;
    height: 20px;
    width: 20px;
    border-radius: 50%;
  
    cursor: pointer;
    margin: 15px 7px 0px;
  
    background-color: #c4c4c4;
  }
  
  .slideshowDot.active {
    background-color: #6a0dad;
  }
  
  /* Responsive Slideshow */
  @media (max-width: 768px) {
    .slideshow {
      margin: 30px auto 15px auto;
    }

    .slide {
      height: 310px;
      border-radius: 30px;
    }
    
    .slideshowDot {
      height: 16px;
      width: 16px;
      margin: 12px 5px 0px;
    }
  }
  
  @media (max-width: 480px) {
    .slideshow {
      margin: 20px auto 10px auto;
    }

    .slide {
      height: 310px;
      border-radius: 20px;
    }
    
    .slideshowDot {
      height: 14px;
      width: 14px;
      margin: 10px 4px 0px;
    }
  }