#profilePic {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  border: 4px solid var(--glass-border);
  box-shadow: 
    0 0 40px rgba(102, 126, 234, 0.3),
    0 20px 60px rgba(0, 0, 0, 0.3);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  margin-top: 3%;
}

#profilePic:hover {
  transform: scale(1.05) translateY(-5px);
  box-shadow: 
    0 0 60px rgba(102, 126, 234, 0.5),
    0 25px 80px rgba(0, 0, 0, 0.4);
  border-color: rgba(102, 126, 234, 0.5);
}

#presentationContainer {
  animation: topToBottom ease 2s;
  -webkit-animation: topToBottom ease 2s;
  z-index: 2;
  position: relative;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
  padding: var(--spacing-xl) var(--spacing-md);
  padding-top: 120px;
}

#hello {
  font: 600 clamp(1.5rem, 3vw, 2.5rem) var(--font-heading);
  letter-spacing: 1px;
  color: var(--text-primary);
  text-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
  margin-bottom: var(--spacing-lg);
  position: absolute;
  top: var(--spacing-xl);
  left: 0;
  right: 0;
  text-align: center;
}

#hello b {
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 800;
  display: inline-block;
  animation: giovanni-shake-infinite 5s ease-in-out infinite;
  position: relative;
  vertical-align: baseline;
  will-change: transform;
}

#hello b::after {
  content: 'Giovanni';
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(90deg, 
    transparent 0%, 
    rgba(255, 255, 255, 0.8) 50%,
    transparent 100%);
  background-size: 300% 100%;
  background-position: -100% 0;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: shine-sweep-infinite 5s ease-in-out infinite;
  pointer-events: none;
  font-weight: 800;
  will-change: transform;
}

.socialIcon {
  margin: 20px;
  opacity: 0.7;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.2));
  color: white;
}

.socialIconsContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0;
}

.socialIcon:hover {
  opacity: 1;
  transform: translateY(-5px) scale(1.1);
  filter: drop-shadow(0 8px 16px rgba(102, 126, 234, 0.4));
}

.socialLink {
  display: inline-block;
}

.socialLink:hover {
  color: white;
}

.newsletter-container {
  position: relative;
  border-radius: 16px;
  margin-top: var(--spacing-lg);
  margin-left: auto;
  margin-right: auto;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--glass-border);
  padding: 20px 24px;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  max-width: 400px;
  box-shadow: 
    0 0 40px rgba(140, 255, 180, 0.2),
    var(--shadow-lg);
  animation: newsletter-border-blink 2s ease-in-out infinite;
}

.newsletter-container:hover {
  transform: translateY(-3px);
  box-shadow: 
    0 0 50px rgba(140, 255, 180, 0.3),
    0 20px 40px rgba(0, 0, 0, 0.3);
}

.newsletter-content {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.newsletter-header {
  text-align: center;
}

.newsletter-title-link {
  text-decoration: none;
  cursor: pointer;
  transition: filter 0.3s ease;
  display: inline-block;
}

.newsletter-title-link:hover .newsletter-title {
  filter: brightness(1.3) drop-shadow(0 0 12px rgba(140, 255, 180, 0.6));
}

.newsletter-title {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--text-primary);
  margin: 0 0 4px 0;
  background: linear-gradient(135deg, rgb(140, 255, 180) 0%, rgb(100, 240, 160) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.newsletter-icon {
  -webkit-text-fill-color: initial;
  color: inherit;
  background: none;
  -webkit-background-clip: initial;
  background-clip: initial;
  display: inline-block;
}

.newsletter-subtitle {
  font-size: 0.875rem;
  color: var(--text-secondary);
  margin: 0;
}

.newsletter-form {
  display: flex;
  gap: 8px;
}

.newsletter-input {
  flex: 1;
  padding: 10px 14px;
  border-radius: 10px;
  border: 1px solid var(--glass-border);
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
  font-size: 0.875rem;
  transition: all 0.3s ease;
  outline: none;
}

.newsletter-input:focus {
  border-color: rgb(140, 255, 180);
  background: rgba(255, 255, 255, 0.08);
  box-shadow: 0 0 0 3px rgba(140, 255, 180, 0.1);
}

.newsletter-input::placeholder {
  color: var(--text-secondary);
  opacity: 0.6;
}

.newsletter-button {
  padding: 10px 20px;
  border-radius: 10px;
  border: none;
  background: linear-gradient(135deg, rgb(140, 255, 180) 0%, rgb(100, 240, 160) 100%);
  color: rgb(8, 55, 23);
  font-weight: 700;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 12px rgba(140, 255, 180, 0.3);
  white-space: nowrap;
}

.newsletter-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(140, 255, 180, 0.4);
}

.newsletter-button:active {
  transform: translateY(0);
}

@media (max-width: 768px) {
  .newsletter-container {
    max-width: 100%;
  }
  
  .newsletter-form {
    flex-direction: column;
  }
  
  .newsletter-button {
    width: 100%;
  }
}

@keyframes newsletter-border-blink {
  0%, 100% {
    border-color: var(--glass-border);
    box-shadow: 
      0 0 40px rgba(140, 255, 180, 0.2),
      var(--shadow-lg);
  }
  50% {
    border-color: rgba(140, 255, 180, 0.6);
    box-shadow: 
      0 0 40px rgba(140, 255, 180, 0.5),
      0 0 20px rgba(140, 255, 180, 0.4),
      var(--shadow-lg);
  }
}

.hide-on-landscape {
  @media (orientation: landscape) and (hover: none) and (pointer: coarse) {
    display: none;
  }
}

/*
* ANIMATIONS
*/

@keyframes fadein {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes giovanni-bounce {
  0% {
    transform: translateY(0);
  }
  10% {
    transform: translateY(-8px);
  }
  25% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-5px);
  }
  55%, 100% {
    transform: translateY(0);
  }
}

@keyframes giovanni-shake {
  0%, 100% {
    transform: translateX(0) rotate(0deg);
  }
  10% {
    transform: translateX(-6px) rotate(-2deg);
  }
  20% {
    transform: translateX(6px) rotate(2deg);
  }
  30% {
    transform: translateX(-6px) rotate(-2deg);
  }
  40% {
    transform: translateX(6px) rotate(2deg);
  }
  50% {
    transform: translateX(-4px) rotate(-1.5deg);
  }
  60% {
    transform: translateX(4px) rotate(1.5deg);
  }
  70% {
    transform: translateX(-3px) rotate(-1deg);
  }
  80% {
    transform: translateX(3px) rotate(1deg);
  }
  90% {
    transform: translateX(-1px) rotate(-0.5deg);
  }
}

@keyframes giovanni-shake-mobile {
  0%, 100% {
    transform: translateX(0) rotate(0deg);
  }
  10% {
    transform: translateX(-10px) rotate(-3deg);
  }
  20% {
    transform: translateX(10px) rotate(3deg);
  }
  30% {
    transform: translateX(-10px) rotate(-3deg);
  }
  40% {
    transform: translateX(10px) rotate(3deg);
  }
  50% {
    transform: translateX(-7px) rotate(-2deg);
  }
  60% {
    transform: translateX(7px) rotate(2deg);
  }
  70% {
    transform: translateX(-5px) rotate(-1.5deg);
  }
  80% {
    transform: translateX(5px) rotate(1.5deg);
  }
  90% {
    transform: translateX(-2px) rotate(-1deg);
  }
}

@keyframes giovanni-bounce-shake-mobile {
  /* Bounce phase: 0-40% */
  0% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  7% {
    transform: translateY(-8px) translateX(0) rotate(0deg);
  }
  17% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  27% {
    transform: translateY(-5px) translateX(0) rotate(0deg);
  }
  37%, 44% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
  /* Shake phase: 45-100% */
  47% {
    transform: translateY(0) translateX(-15px) rotate(-4deg);
  }
  52% {
    transform: translateY(0) translateX(15px) rotate(4deg);
  }
  57% {
    transform: translateY(0) translateX(-15px) rotate(-4deg);
  }
  62% {
    transform: translateY(0) translateX(15px) rotate(4deg);
  }
  67% {
    transform: translateY(0) translateX(-12px) rotate(-3deg);
  }
  72% {
    transform: translateY(0) translateX(12px) rotate(3deg);
  }
  77% {
    transform: translateY(0) translateX(-8px) rotate(-2deg);
  }
  82% {
    transform: translateY(0) translateX(8px) rotate(2deg);
  }
  87% {
    transform: translateY(0) translateX(-4px) rotate(-1deg);
  }
  92% {
    transform: translateY(0) translateX(4px) rotate(1deg);
  }
  100% {
    transform: translateY(0) translateX(0) rotate(0deg);
  }
}

@keyframes shine-sweep {
  0% {
    background-position: -100% 0;
  }
  100% {
    background-position: 100% 0;
  }
}

@keyframes shine-sweep-infinite {
  0%, 100% {
    background-position: -100% 0;
  }
  0.1% {
    background-position: -100% 0;
  }
  16% {
    background-position: 100% 0;
  }
  16.1%, 100% {
    background-position: 100% 0;
  }
}

@keyframes giovanni-shake-infinite {
  0%, 15.9%, 100% {
    transform: translateX(0) rotate(0deg);
  }
  16% {
    transform: translateX(0) rotate(0deg);
  }
  17% {
    transform: translateX(-6px) rotate(-2deg);
  }
  18% {
    transform: translateX(6px) rotate(2deg);
  }
  19% {
    transform: translateX(-6px) rotate(-2deg);
  }
  20% {
    transform: translateX(6px) rotate(2deg);
  }
  21% {
    transform: translateX(-4px) rotate(-1.5deg);
  }
  22% {
    transform: translateX(4px) rotate(1.5deg);
  }
  23% {
    transform: translateX(-3px) rotate(-1deg);
  }
  24% {
    transform: translateX(3px) rotate(1deg);
  }
  25% {
    transform: translateX(-1px) rotate(-0.5deg);
  }
  26%, 100% {
    transform: translateX(0) rotate(0deg);
  }
}

@keyframes light-pass {
  0% {
    left: -150%;
    opacity: 0;
  }
  5% {
    opacity: 1;
  }
  95% {
    opacity: 1;
  }
  100% {
    left: 150%;
    opacity: 0;
  }
}

@keyframes topToBottom {
  0% {
    opacity: 0;
    transform: translateY(-100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Mobile Optimizations */
@media (max-width: 768px) {
  #profilePic {
    width: 320px;
    height: 320px;
    margin-top: 8%;
  }
  
  #presentationContainer {
    padding: var(--spacing-md) var(--spacing-sm);
    padding-top: 80px;
    max-width: 100%;
    width: 100%;
    box-sizing: border-box;
  }
  
  #hello {
    position: absolute;
    top: 10px;
    margin-bottom: var(--spacing-md);
    padding: 0 var(--spacing-sm);
    font-size: clamp(2.4rem, 4.8vw, 4rem);
  }
  
  .socialIconsContainer {
    margin-top: calc(var(--spacing-xl) * 1.5);
  }
  
  .socialIcon {
    margin: 20px;
  }
  
  .socialIcon[style*="50px"] {
    width: 50px !important;
    height: 50px !important;
  }
  
  .socialIcon[style*="45px"] {
    width: 45px !important;
    height: 45px !important;
  }
  
  .socialIcon[style*="56px"] {
    width: 60px !important;
    height: 60px !important;
  }
  
  .newsletter-container {
    margin-top: var(--spacing-md);
    padding: 16px 20px;
    max-width: calc(100% - 32px);
    width: 100%;
    box-sizing: border-box;
  }
  
  .newsletter-title {
    font-size: 1.1rem;
  }
  
  .newsletter-subtitle {
    font-size: 0.8rem;
  }
  
  .socialIcon:hover {
    transform: translateY(-3px) scale(1.05);
  }
}

@media (max-width: 480px) {
  #profilePic {
    width: 310px;
    height: 310px;
    margin-top: 11%;
  }
  
  #presentationContainer {
    padding: var(--spacing-sm);
    padding-top: 60px;
    max-width: 100%;
    width: 100%;
    box-sizing: border-box;
  }
  
  .socialIcon {
    margin: 20px;
  }
  
  .socialIcon[style*="50px"] {
    width: 50px !important;
    height: 50px !important;
  }
  
  .socialIcon[style*="45px"] {
    width: 45px !important;
    height: 45px !important;
  }
  
  .socialIcon[style*="56px"] {
    width: 60px !important;
    height: 60px !important;
  }
  
  #hello {
    position: absolute;
    top: 5px;
    font-size: clamp(2rem, 8vw, 2.8rem);
    margin-bottom: var(--spacing-sm);
  }
  
  .socialIconsContainer {
    margin-top: calc(var(--spacing-xl) * 2);
  }
  
  .newsletter-container {
    padding: 14px 16px;
    margin-top: var(--spacing-sm);
    max-width: calc(100% - 24px);
    width: 100%;
    box-sizing: border-box;
  }
  
  .newsletter-title {
    font-size: 1rem;
  }
  
  .newsletter-subtitle {
    font-size: 0.75rem;
    line-height: 1.4;
  }
  
  .newsletter-input,
  .newsletter-button {
    font-size: 0.8rem;
    padding: 8px 12px;
  }
}
