a {
    color: var(--accent-cyan);
    text-decoration: none;
    transition: all 0.3s ease;
}

a:hover {
    color: var(--accent-primary);
    text-decoration: none;
}

#baby {
    max-width: 100%;
    width: auto;
    height: auto;
    display: block;
    margin: 0 auto;
    border-radius: 8px;
    box-shadow: var(--shadow-md);
}

.celebration-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 9999;
}

.confetti {
    position: absolute;
    font-size: 2rem;
    animation: confettiFall 2s ease-out forwards;
    opacity: 0;
}

.confetti-0 { top: 50%; left: 50%; animation-delay: 0s; }
.confetti-1 { top: 50%; left: 50%; animation-delay: 0.1s; }
.confetti-2 { top: 50%; left: 50%; animation-delay: 0.2s; }
.confetti-3 { top: 50%; left: 50%; animation-delay: 0.3s; }
.confetti-4 { top: 50%; left: 50%; animation-delay: 0.4s; }

@keyframes confettiFall {
    0% {
        transform: translate(0, 0) rotate(0deg) scale(0);
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    100% {
        transform: translate(
            calc(var(--x, 0) * 1px),
            calc(var(--y, 0) * 1px)
        ) rotate(720deg) scale(1.5);
        opacity: 0;
    }
}

.confetti:nth-child(1) { --x: 200; --y: -300; }
.confetti:nth-child(2) { --x: -200; --y: -300; }
.confetti:nth-child(3) { --x: 300; --y: -200; }
.confetti:nth-child(4) { --x: -300; --y: -200; }
.confetti:nth-child(5) { --x: 150; --y: -350; }
.confetti:nth-child(6) { --x: -150; --y: -350; }
.confetti:nth-child(7) { --x: 250; --y: -100; }
.confetti:nth-child(8) { --x: -250; --y: -100; }
.confetti:nth-child(9) { --x: 100; --y: -250; }
.confetti:nth-child(10) { --x: -100; --y: -250; }
.confetti:nth-child(11) { --x: 350; --y: -150; }
.confetti:nth-child(12) { --x: -350; --y: -150; }
.confetti:nth-child(13) { --x: 180; --y: -320; }
.confetti:nth-child(14) { --x: -180; --y: -320; }
.confetti:nth-child(15) { --x: 280; --y: -180; }
.confetti:nth-child(16) { --x: -280; --y: -180; }
.confetti:nth-child(17) { --x: 120; --y: -280; }
.confetti:nth-child(18) { --x: -120; --y: -280; }
.confetti:nth-child(19) { --x: 320; --y: -220; }
.confetti:nth-child(20) { --x: -320; --y: -220; }

.icon {
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.icon:hover {
    transform: scale(1.1) rotate(5deg);
    box-shadow: 0 0 20px rgba(52, 81, 214, 0.4), var(--shadow-lg);
}

.iconChildhood {
    position: relative;
    overflow: hidden;
}

.iconChildhood::before {
    content: '';
    position: absolute;
    top: 150%;
    left: -150%;
    width: 200%;
    height: 200%;
    background: linear-gradient(
        45deg,
        transparent 0%,
        transparent 30%,
        rgba(255, 255, 255, 0.6) 50%,
        transparent 70%,
        transparent 100%
    );
    animation: metallicShine 2s ease-in-out infinite;
    z-index: 1;
    pointer-events: none;
}

@keyframes metallicShine {
    0%, 14% {
        top: 100%;
        left: -100%;
        opacity: 0;
    }
    15% {
        top: 80%;
        left: -80%;
        opacity: 1;
    }
    35% {
        top: -80%;
        left: 80%;
        opacity: 1;
    }
    36%, 100% {
        top: -100%;
        left: 100%;
        opacity: 0;
    }
}

.iconChildhood:hover {
    transform: scale(1.1) rotate(-5deg);
    box-shadow: 0 0 20px rgba(236, 95, 13, 0.4), var(--shadow-lg);
    background: linear-gradient(135deg, rgba(236, 95, 13, 0.2), rgba(245, 120, 50, 0.2));
    color: rgb(236, 95, 13);
    border-color: rgba(236, 95, 13, 0.5);
}

