    background: linear-gradient(135deg, rgba(26, 116, 8, 0.2), rgba(50, 200, 30, 0.2));
    color: rgb(26, 200, 8);
    border-color: rgba(26, 116, 8, 0.5);
}

.iconQuantycaJob:hover {
    transform: scale(1.1) rotate(5deg);
    box-shadow: 0 0 20px rgba(145, 5, 121, 0.4), var(--shadow-lg);
    background: linear-gradient(135deg, rgba(145, 5, 121, 0.2), rgba(200, 50, 180, 0.2));
