.App {
  text-align: center;
  position: relative;
  overflow-x: hidden;
}

.App::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    radial-gradient(circle at 20% 50%, rgba(102, 126, 234, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, rgba(167, 139, 250, 0.1) 0%, transparent 50%);
  pointer-events: none;
  z-index: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.languageFlag {
  opacity: 0.7;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  filter: grayscale(0.3);
}

.languageFlag:hover {
  opacity: 1;
  cursor: pointer;
  filter: grayscale(0);
  transform: scale(1.1);
}

#presentation {
  min-height: 100vh;
  min-height: 100dvh;
  background: var(--hero-gradient);
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
}

#presentation::before,
#presentation::after {
  pointer-events: none;
}

/* Raggi di luce sott'acqua */
#presentation .light-rays {
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: 
    linear-gradient(110deg, transparent 0%, transparent 46%, rgba(167, 139, 250, 0.08) 48%, rgba(167, 139, 250, 0.18) 49.5%, rgba(167, 139, 250, 0.2) 50%, rgba(167, 139, 250, 0.18) 50.5%, rgba(167, 139, 250, 0.08) 52%, transparent 54%, transparent 100%),
    linear-gradient(125deg, transparent 0%, transparent 36%, rgba(102, 126, 234, 0.07) 38%, rgba(102, 126, 234, 0.15) 39.5%, rgba(102, 126, 234, 0.17) 40%, rgba(102, 126, 234, 0.15) 40.5%, rgba(102, 126, 234, 0.07) 42%, transparent 44%, transparent 100%),
    linear-gradient(95deg, transparent 0%, transparent 56%, rgba(118, 75, 162, 0.06) 58%, rgba(118, 75, 162, 0.14) 59.5%, rgba(118, 75, 162, 0.16) 60%, rgba(118, 75, 162, 0.14) 60.5%, rgba(118, 75, 162, 0.06) 62%, transparent 64%, transparent 100%),
    linear-gradient(140deg, transparent 0%, transparent 26%, rgba(139, 92, 246, 0.065) 28%, rgba(139, 92, 246, 0.13) 29.5%, rgba(139, 92, 246, 0.15) 30%, rgba(139, 92, 246, 0.13) 30.5%, rgba(139, 92, 246, 0.065) 32%, transparent 34%, transparent 100%),
    linear-gradient(88deg, transparent 0%, transparent 15%, rgba(124, 58, 237, 0.05) 17%, rgba(124, 58, 237, 0.1) 18.5%, rgba(124, 58, 237, 0.12) 19%, rgba(124, 58, 237, 0.1) 19.5%, rgba(124, 58, 237, 0.05) 21%, transparent 23%, transparent 100%);
  animation: light-rays-underwater 30s ease-in-out infinite;
  opacity: 0.85;
  z-index: 0;
  pointer-events: none;
  will-change: transform;
  transform: translateZ(0);
}

#presentation > #presentationContainer {
  position: relative;
  z-index: 3;
}

