@keyframes gradient-shift {
  0%, 100% { opacity: 0.5; transform: scale(1); }
  50% { opacity: 0.8; transform: scale(1.1); }
}

@keyframes stars-move-1 {
  0% { 
    transform: translateY(600px) translateX(100px) scale(0.7);
    opacity: 0;
  }
  15% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  100% { 
    transform: translateY(-600px) translateX(-150px) scale(1.3);
    opacity: 0;
  }
}

@keyframes stars-move-2 {
  0% { 
    transform: translateY(-500px) translateX(-80px) scale(1.2);
    opacity: 0;
  }
  12% {
    opacity: 1;
  }
  88% {
    opacity: 1;
  }
  100% { 
    transform: translateY(700px) translateX(120px) scale(0.8);
    opacity: 0;
  }
}

@keyframes stars-pulse-1 {
  0%, 100% { 
    filter: blur(1.5px) brightness(1);
  }
  15% {
    filter: blur(3px) brightness(1.6);
  }
  30% {
    filter: blur(1px) brightness(1.2);
  }
  55% { 
    filter: blur(4px) brightness(2);
  }
  70% {
    filter: blur(2px) brightness(1.3);
  }
}

@keyframes stars-pulse-2 {
  0%, 100% { 
    filter: blur(2px) brightness(1.1);
  }
  20% {
    filter: blur(1px) brightness(1.4);
  }
  45% { 
    filter: blur(3.5px) brightness(1.8);
  }
  65% {
    filter: blur(2.5px) brightness(1.2);
  }
  85% {
    filter: blur(1.5px) brightness(1.6);
  }
}

@keyframes space-rays {
  0% {
    transform: translateX(0) translateY(0) rotate(0deg);
  }
  100% {
    transform: translateX(50%) translateY(50%) rotate(5deg);
  }
}

@keyframes light-rays-underwater {
  0% {
    transform: translateX(-20%) translateY(-15%) scale(1, 1) skewY(0deg);
  }
  25% {
    transform: translateX(-5%) translateY(5%) scale(1.02, 0.98) skewY(0.3deg);
  }
  50% {
    transform: translateX(15%) translateY(20%) scale(0.98, 1.02) skewY(-0.2deg);
  }
  75% {
    transform: translateX(10%) translateY(8%) scale(1.01, 0.99) skewY(0.2deg);
  }
  100% {
    transform: translateX(-20%) translateY(-15%) scale(1, 1) skewY(0deg);
  }
}

@keyframes underwater-wave {
  0%, 100% {
    transform: scale(1, 1) skewY(0deg);
  }
  25% {
    transform: scale(1.02, 0.98) skewY(0.3deg);
  }
  50% {
    transform: scale(0.98, 1.02) skewY(-0.2deg);
  }
  75% {
    transform: scale(1.01, 0.99) skewY(0.2deg);
  }
}

#biography {
  min-height: 50vh;
}

#skills {
  min-height: 60vh;
}

.App-header {
  display: flex;
  height: fit-content;
  flex-direction: column;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#navbar {
  position: absolute;
}

.fade-in-section {
  opacity: 0;
  transform: translateY(60px);
  visibility: hidden;
  transition: opacity 0.8s cubic-bezier(0.4, 0, 0.2, 1), 
              transform 1s cubic-bezier(0.4, 0, 0.2, 1);
  will-change: opacity, transform, visibility;
}

.fade-in-section.is-visible {
  opacity: 1;
  transform: translateY(0);
  visibility: visible;
}

#nav-wrap ul, #nav-wrap li, #nav-wrap a {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
}

/* nav-wrap */
#nav-wrap {
  font: 13px var(--font-heading);
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  margin: 0 auto;
  z-index: 100;
  background: rgba(15, 15, 30, 0.8);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-bottom: 1px solid var(--glass-border);
  position: fixed;
  left: 0;
  top: 0;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}

#nav-wrap:hover {
  background: rgba(15, 15, 30, 0.95);
  box-shadow: 0 8px 40px rgba(0, 0, 0, 0.2);
}

.opaque { 
  background: rgba(26, 26, 46, 0.98);
  backdrop-filter: blur(20px);
}

/* hide toggle button */
#nav-wrap > a.mobile-btn { display: none; }

ul#nav {
  min-height: 50px;
  width: auto;
  justify-content: center;
  text-align: center;
  padding: 0;
  margin: 0;
}

ul#nav li {
  position: relative;
  list-style: none;
  height: 50px;
  display: inline-block;
  margin: 0 5px;
}

/* Links */
ul#nav li a,
ul#nav li .smoothscroll {
  display: inline-block;
  padding: 8px 20px;
  line-height: 34px;
  text-decoration: none;
  text-align: center;
  color: var(--text-secondary);
  cursor: pointer;
  border-radius: 8px;
  font-weight: 600;
  position: relative;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

ul#nav li a::before,
ul#nav li .smoothscroll::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--primary-gradient);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: -1;
  border-radius: 8px;
}

ul#nav li a:active,
ul#nav li .smoothscroll:active { 
  background-color: transparent !important; 
  transform: scale(0.95);
}

ul#nav li.current a,
ul#nav li.current .smoothscroll { 
  color: var(--text-primary);
  background: var(--glass-bg);
  box-shadow: var(--shadow-glow);
}

ul#nav li a:hover,
ul#nav li .smoothscroll:hover {
  color: var(--text-primary);
  font-weight: 600;
  text-shadow: 0 0 20px rgba(102, 126, 234, 0.6),
               0 0 40px rgba(102, 126, 234, 0.3);
}

ul#nav li a:hover::before,
ul#nav li .smoothscroll:hover::before {
  opacity: 0;
}

/* mobile wide/smaller tablets
---------------------------------------------------------------------- */

@media only screen and (max-width: 767px) {

  /* mobile navigation
  -------------------------------------------------------------------- */
  
  html, body {
    overflow-x: hidden;
    max-width: 100%;
  }
  
  .languageFlag {
    opacity: 1;
  }

  #languages {
    width: auto;
    min-width: 90px;
    height: 40px;
    background: rgba(26, 26, 46, 0.95);
    backdrop-filter: blur(20px);
    text-shadow: none;
    bottom: 0;
    border-radius: 12px;
    border: 1px solid var(--glass-border);
    padding: 8px 12px;
  }

  #mailContainer {
    width: 45px;
    height: 45px;
    background: rgba(26, 26, 46, 0.95);
    backdrop-filter: blur(20px);
    text-shadow: none;
    position: relative;
    bottom: 0;
    border-radius: 12px;
    border: 1px solid var(--glass-border);
    padding: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  #nav-wrap {
     font: 13px var(--font-heading);
     background: transparent !important;
     letter-spacing: 1.5px;  
     width: auto;
     position: fixed;
     top: 0;
     right: 0;
     backdrop-filter: none;
     border: none !important;
     border-bottom: none !important;
     box-shadow: none !important;
  }
  
  #nav-wrap > a {
    width: 48px;
    height: 48px;
    text-align: left;
    background: rgba(26, 26, 46, 0.95);
    backdrop-filter: blur(20px);
    position: relative;
    border: 1px solid var(--glass-border);
    float: right;
    font: 0/0 a;
    text-shadow: none;
    color: transparent;
    position: relative;
    top: 0px;
    right: 30px;
    border-radius: 12px;
    transition: all 0.3s ease;
  }
  
  #nav-wrap > a:hover {
    background: rgba(26, 26, 46, 1);
    box-shadow: var(--shadow-md);
  }

 #nav-wrap > a:before,
  #nav-wrap > a:after {
   position: absolute;
   border: 2px solid #fff;
   top: 35%;
   left: 25%;
   right: 25%;
   content: '';
 }
  #nav-wrap > a:after { top: 60%; }

  /* toggle buttons */
 #nav-wrap:not( :target ) > a:first-of-type,
 #nav-wrap:target > a:last-of-type  {
    display: block;
    border-radius: 12px;
 }

  /* hide menu panel */
  #nav-wrap ul#nav {
     height: auto;
     display: none;
     clear: both;
     width: auto; 
     float: right;     
     position: relative;
     top: 12px;
     right: 0;
  }

  /* display menu panels */
 #nav-wrap:target > ul#nav	{
    display: block;
    padding: 30px 20px 48px 20px;
    background: rgba(26, 26, 46, 0.98);
    backdrop-filter: blur(30px);
    margin: 0 30px;
    clear: both;
    border-radius: 16px;
    border: 1px solid var(--glass-border);
    box-shadow: var(--shadow-xl);
  }

  ul#nav li {
     display: block;
     height: auto;      
     margin: 0 auto; 
     padding: 0 4%;           
     text-align: left;
     border-bottom: 1px solid rgba(255, 255, 255, 0.1);
     border-bottom-style: solid; 
  }
  
  ul#nav li:last-child {
    border-bottom: none;
  }
 
  ul#nav li a {  
     display: block;  
     padding: 12px 8px;      
     margin: 0; 
     line-height: 1.5;
     border: none;
     border-radius: 8px;
     transition: all 0.3s ease;
  }
  
  ul#nav li a:hover {
    background: var(--glass-bg);
    transform: translateX(8px);
  }

}

#languages { 
  position: fixed; 
  margin-left: 20px; 
  z-index: 999;
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
