#websiteVersion {
  margin-top: 0;
  margin-bottom: 0;
  padding: var(--spacing-md);
  color: var(--text-muted);
  font-size: 0.875rem;
  background: var(--bg-dark-secondary);
  text-align: center;
}

#websiteVersion span {
  cursor: pointer;
  transition: all 0.3s ease;
}

#websiteVersion span:hover {
  color: var(--accent-primary);
  text-decoration: none;
}

/* Responsive optimizations for presentation section */
@media (max-width: 768px) {
  #presentation .light-rays {
    opacity: 0.5;
  }
  
  #presentation::before,
  #presentation::after {
    filter: blur(3px);
    opacity: 0.6;
  }
}

@media (max-width: 480px) {
  #presentation .light-rays {
    opacity: 0.3;
  }
  
  #presentation::before,
  #presentation::after {
    filter: blur(4px);
    opacity: 0.4;
  }
}
